import { Button, Form, Space } from "antd";
import { useContext, useEffect, useState } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { incentivoValues } from "../../../data/initalValues";
import MainModal from "../../modal/MainModal";
import { notificacion } from "../../../helpers/mensajes";
import { AiOutlineForm, AiOutlineTeam } from "react-icons/ai";
import { modalIncentivo } from "../../../data/FormValues";
import dayjs from "dayjs";
import ModalJuntarTeletrans from "./ModalJuntarTeletrans";
import ButtonAdd from "../../Button/ButtonAdd";

function ModalIncentivo({ actualizarTabla, modal, setModal }) {
	const [form] = Form.useForm();

	const { createData, getData, setDataToEdit, dataToEdit, updateData } =
		useContext(CrudContext);

	const [incentivo, setIncentivo] = useState(incentivoValues);
	const [cargando, setCargando] = useState(false);
	const [trabajadores, setTrabajadores] = useState([]);

	const getAllTrabajadores = async () => {
		const response = await getData("incentivo/trabajadores");
		setTrabajadores(response.data);
	};

	useEffect(() => {
		getAllTrabajadores();
	}, []);

	useEffect(() => {
		if (dataToEdit) {
			setIncentivo({
				...dataToEdit,
				unidad_produccion: dataToEdit.unidad_produccion,
				nombre: dataToEdit.trabajadores[0].nombre,
				teletrans: parseFloat(dataToEdit.trabajadores[0].teletrans),
				observacion: dataToEdit.observacion,
				fecha_pago: dayjs(dataToEdit.fecha_pago).format("YYYY-MM-DD"),
			});
			form.setFieldsValue({
				...dataToEdit,
				unidad_produccion: dataToEdit.unidad_produccion,
				nombre: dataToEdit.trabajadores[0].nombre,
				teletrans: parseFloat(dataToEdit.trabajadores[0].teletrans),
				observacion: dataToEdit.observacion,
				fecha_pago: dayjs(dataToEdit.fecha_pago).format("YYYY-MM-DD"),
			});
		} else {
			setIncentivo(incentivoValues);
		}
	}, [dataToEdit]);

	const handleData = (e, text) => {
		if (!text && e.target) {
			const { name, value } = e.target;
			form.setFieldsValue({
				[name]: value,
			});
			setIncentivo((values) => {
				return { ...values, [name]: value };
			});
		} else {
			form.setFieldsValue({
				[text]: e,
			});
			setIncentivo((values) => {
				return { ...values, [text]: e };
			});
		}
	};

	const handleSubmit = async () => {
		const route = "pago/programacion/multiple";
		setCargando(true);
		const trabajadores = [
			{
				contrato_id: incentivo.contrato_id || "",
				teletrans: parseFloat(incentivo.teletrans) || 0,
				trabajador_dni: incentivo.trabajador_dni,
			},
		];
		const incentivoData = {
			unidad_produccion: incentivo.unidad_produccion || "",
			observacion: incentivo.observacion || "",
			fecha_pago: dayjs(incentivo.fecha_pago).format("YYYY-MM-DD") || "",
			tipo: incentivo.tipo || "incentivo",
			teletrans: parseFloat(incentivo.teletrans) || 0,
			trabajadores: trabajadores,
		};
		if (dataToEdit) {
			const response = await updateData(
				incentivoData,
				dataToEdit.pago_id,
				"pago"
			);
			if (response) {
				notificacion(response.status, response.msg);
				actualizarTabla();
				closeModal();
			}
		} else {
			const response = await createData(incentivoData, route);
			if (response) {
				notificacion(response.status, response.msg);
				actualizarTabla();
				closeModal();
			}
		}
	};
	const closeModal = () => {
		setModal(false);
		setDataToEdit(null);
		setCargando(false);
		setIncentivo(incentivoValues);
	};

	const formData = modalIncentivo(
		incentivo,
		handleData,
		trabajadores,
		dataToEdit
	);

	const [modalTeletrans, setModalTeletrans] = useState(false);
	const handleOpenModalTeletrans = () => {
		setModalTeletrans(true);
	};

	const handleCloseModalTeletrans = () => {
		setModalTeletrans(false);
	};

	return (
		<>
			<MainModal
				className={"modal-usuario"}
				title={dataToEdit ? "Editar incentivo" : "Registrar incentivo"}
				open={modal}
				width={400}
				closeModal={closeModal}
			>
				{!dataToEdit && (
					<Space
						direction="horizontal"
						style={{ width: "100%", justifyContent: "end" }}
					>
						<ButtonAdd
							title="Juntar Teletrans"
							onClick={handleOpenModalTeletrans}
							icon={<AiOutlineTeam />}
						/>
					</Space>
				)}
				<Form
					form={form}
					className="modal-body"
					onFinish={handleSubmit}
					layout="horizontal"
				>
					{formData.map((item, i) => (
						<Form.Item
							className={item.className}
							key={i}
							name={item.name}
							rules={item.rules}
							style={{ marginBottom: "8px" }}
						>
							<>
								{item.label}
								{item.type}
							</>
						</Form.Item>
					))}

					<Form.Item className="button-container">
						<Button
							htmlType="submit"
							icon={<AiOutlineForm />}
							loading={cargando ? true : false}
						>
							{dataToEdit ? "Editar" : "Registrar"}
						</Button>
					</Form.Item>
				</Form>
			</MainModal>
			{modalTeletrans && (
				<ModalJuntarTeletrans
					open={modalTeletrans}
					closeModal={handleCloseModalTeletrans}
					trabajadores={trabajadores}
					actualizarTabla={actualizarTabla}
					closeModalSubmit={closeModal}
				/>
			)}
		</>
	);
}
export default ModalIncentivo;
